:root {
  --primary: #25092c;
  --secondary: #9be8a1;
  --lovelypink: #fe8daa;
  --complimentsec: #e3ae96;
}

* {
  box-sizing: border-box;
}

.sliders {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 95vh;
  position: relative;
  overflow: hidden;
  margin-bottom: 100px;
  gap: 50px;
}

.slides img {
  -o-object-fit: cover;
     object-fit: cover;
  width: auto;
  height: 200px;
  max-height: 300px;
}

.slides {
  /* scale: 0.5; */
  display: flex;
  justify-content: space-evenly;
  /* top: 0; */
  opacity: 0;
  transform: translateX(-100%);
  transition: all 0.5s ease;
  /* padding: 20px; */
  /* margin-bottom: 300px; */
}

.contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  /* left: 70%; */
  /* opacity : 0; */
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 1rem;
  /* border-bottom: 2px solid var(--secondary); */
  background: rgba(138, 108, 138, 0.2);
  letter-spacing: 0.25px;
  word-spacing: 1.5px;
  /* font-size: larger; */
  font-weight: 100;
  font-size: 30;
  cursor: pointer;
  transition: all 1 s ease;
  border-radius: 10px;
  /* border: 3px solid rgb(54, 15, 108); */
}
.contents h5 {
  color: rgb(10, 45, 58);
  font-weight: 600;
}
.contents hr {
  background-color: black;
}

@-webkit-keyframes slide-up {
  25% {
    visibility: visible;
    top: 0%;
  }
  50% {
    visibility: visible;
    top: 25%;
  }
  75% {
    visibility: hidden;
    top: 50%;
  }
  100% {
    visibility: visible;
    top: 75%;
  }
}

@keyframes slide-up {
  25% {
    visibility: visible;
    top: 0%;
  }
  50% {
    visibility: visible;
    top: 25%;
  }
  75% {
    visibility: hidden;
    top: 50%;
  }
  100% {
    visibility: visible;
    top: 75%;
  }
}
.butt {
  /* width: max-content; */
  /* height: 3%; */
  font-size: 20px;
  font-weight: 500;
  /* padding: 8px; */
  opacity: 1;
  cursor: pointer;
  z-index: 111;
  transition: all 0.5s ease-in-out;
  /* border: 3px solid rgb(54, 15, 108); */
}

.butt:hover {
  /* font-size: 35px; */
}

.button-div {
  display: flex;
  justify-content: space-between;
  position: absolute;
  /* right: 2%; */
  bottom: 1.5%;
  width: 100%;
}

@-webkit-keyframes vibrate {
  0% {
    background-color: #9be8a1;
  }
  10% {
    background-color: #87d58d;
  }
  20% {
    background-color: #52a659;
  }
  30% {
    background-color: #380f38;
    color: var(--secondary);
  }
  40% {
    background-color: #1b5a20;
  }
  50% {
    background-color: #07350b;
  }
  60% {
    background-color: #15591a;
  }
  70% {
    background-color: #257a2c;
  }
  80% {
    background-color: #469f4e;
  }
  90% {
    background-color: #79c37f;
  }
  100% {
    background-color: #9be8a1;
  }
}

@keyframes vibrate {
  0% {
    background-color: #9be8a1;
  }
  10% {
    background-color: #87d58d;
  }
  20% {
    background-color: #52a659;
  }
  30% {
    background-color: #380f38;
    color: var(--secondary);
  }
  40% {
    background-color: #1b5a20;
  }
  50% {
    background-color: #07350b;
  }
  60% {
    background-color: #15591a;
  }
  70% {
    background-color: #257a2c;
  }
  80% {
    background-color: #469f4e;
  }
  90% {
    background-color: #79c37f;
  }
  100% {
    background-color: #9be8a1;
  }
}
.slides img:hover .arrow {
  visibility: visible;
}

.arrows {
  border: 1px solid var(--secondary);
  border-radius: 50%;
  background: var(--binance_ash);
  color: var(--binance_white);
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  position: absolute;
  top: 50%;
  z-index: 999;
  transition: all 0.3s ease-in;
  opacity: 0.5;
}

.arrow:hover {
  background: var(--secondary);
  color: var(--primary);
  /* width: 2.2rem;
  height: 2.2rem; */
  scale: 1.25;
  opacity: 0.85;
}

.current {
  opacity: 1;
  transform: translateX(0);
  /* background-color: #9be8a1; */
  /* scale: 1; */
}

.next {
  right: -3rem;
}

.prev {
  left: -3rem;
}

hr {
  height: 2px;
  width: 100%;
}

.player {
  font-size: 45px;
}

@media screen and (max-width: 770px) {
  .player {
    font-size: 30px;
  }
  .button-div {
    /* right: -20%; */
    bottom: 0%;
  }
  .butt {
    /* scale: 0.5; */
    font-size: 15px;
    border-radius: 7px;
    /* position: relative;
    top: 50px;
    left: -120px; */
    /* background-color: black; */
  }
  .arrow {
    top: 50%;
    width: 1rem;
    height: 1rem;
  }
  .contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    font-size: 15px;
    overflow-wrap: break-word;
  }
  .sliders {
    margin-bottom: 0px;
    flex-direction: column;
  }
  .slides img {
    border-radius: 1px;
    /* opacity: 0; */
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain;
    border: none;
  }
}
